.descriptionBlock {
  display: grid;
  grid-template-columns: auto 1fr;
  padding: 1rem;
  grid-gap: 2rem;

  img {
    object-fit: contain;
    max-height: unset;
  }
}

.background {
  height: auto;
  max-height: 18rem;
  object-fit: cover;
  width: 100%;
  border-radius: 3px 3px 0 0;
}

.avatar {
  position: relative;
}

.customInput {
  input {
    display: none;
  }

  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  padding: 1rem;
  background: rgba(1, 1, 1, .8);
  border: 1px solid #67DBF9;
  border-radius: 3px;
  opacity: 0;
  transition: opacity .2s ease-in-out;
}

.customInput:hover {
    opacity: 1;
}